@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.ui.secondary.item.menu {
  margin: 10px;
  padding: 10px;
}

body {
  background-image: url(/static/media/icon.4d2a68b7.png);
  background-attachment: scroll;
  background-repeat: no-repeat;
  background-size: 40%;
  background-position: calc(100% + 286px) calc(100% - 50px)
}

a {
  color: white;
}

.homepage-container {
  height: 500px;
  width: 600px;
  border-style: dotted;
  border-color: rgb(248, 190, 151);
  border-radius: 30px;
  margin-left: 140px;
  background-color: rgb(241, 241, 220)
}

.homepage-image {
  background-image: url(/static/media/Signin.8a08fdf7.svg);
  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 70%;
  border-width: 10px;
  height: 220px;
  width: 300px;
  margin: 60px 0px 0px 150px;
  border-style: solid;
  border-radius: 100%;
  border-color: rgb(175, 105, 105)
}


p {
  margin: 20px 0px 0px 40px;
  font-size: 5em;
  font-family: "Amatic SC", cursive;
  height: 200px;
  color: rgb(33, 103, 105)
}

/* navbar */

.ui.menu.fixed .item:first-child :last-child {
  margin: 10px 0px;
  width: 20%;
}

i.pink.bullhorn.huge.icon {
  margin-right: 320px
}

div.ui.icon.input {
  margin-right: 200px;
  padding: 0px
}

i.olive.microphone.huge.icon {
  margin-right: 110px
}


.ui.menu.fixed .item:second-child :last-child {
  margin: 10px 0px;
  width: 20%;
}

.ui.menu.fixed .item:third-child :last-child {
  margin: 10px 0px;
  width: 10%;
}

.ui.menu.fixed .item:last-child {
  padding: 10px;
  margin: 10px 400px;
  height: 5.5em;
  width: 150px;
  right: 130px; 
}

.ui.search .prompt {
  border-color: coral;
  width: 40em;
  padding: 10px 20px 10px 40px;
  height: 5.5em;
  font-size: 1em;
}

div.ui.ui.secondary.item.menu {
  background-color: #D8ECE8
  
}

.userInfo {
  float: right;
  opacity: 30%;
  margin-right: 140px;
  margin-top: 20px;
}

.contentBody {
  margin-top: 200px;
  margin-right: 140px;
  height: 600px;
}

div.ui.four.cards {
  width: 1068px;

}

/* recorder graphics */

.cassette {
  margin: 0px 140px 0px 390px;
  height: 380px;
  width: 600px;
  border-radius: 25px;
  background: url(/static/media/cassette.a43d42a3.png);
  border-radius: 25px;
  background-repeat: no-repeat;
  background-size: 600px;
  background-position: center;
  
}

canvas.sound-wave {
  margin-left: 270px;
}

div.fields.recorderButtons {
  margin: 10px 0px 10px 480px
}

.recordingContainer {
  height: 120px;
  margin: 10px 0px 10px 0px;
}

.sound-wave {
  margin: 20px 140px 0px 145px;
  height: 100px;
}

.helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.gear1 {
  z-index: 80px;
  height: 72px;
  width: 72px;
  background: url(/static/media/gears.a831d48c.png);
  background-size: 73px;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  position: absolute;
  left: 527px;
  top: 137px;
}

.gear2 {
  z-index: 80px;
  height: 72px;
  width: 72px;
  background: url(/static/media/gears.a831d48c.png);
  background-size: 73px;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  position: absolute;
  left: 780px;
  top: 137px;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.soundsterContainer {
  width: 600px;
  height: 380px;
  
}

.soundster-input {
  width: 400px;
  margin-left: 100px;
}

div.field.soundster-input-box :first-child{
  margin-top: 17px;
  margin-left: 5px;
}

.category-input {
  margin-top: 2px;
  margin-left: 200px;
  width: 50px;
}

.tag-input {
  margin-top: 110px;
  margin-left: 20px;
}

.home-container {
  margin-left: 200px;
  width: 1000px;
}

